h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input, textarea, button, label {
  font-weight: inherit;
  letter-spacing: .04em;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 1.5;
  display: block;
}

.container {
  min-width: 320px;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.section {
  padding: 40px 0;
}

@media (min-width: 768px) {
  .container {
    min-width: 768px;
    padding: 0 32px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}



















:root {
  --main-text-clr: #111;
  --dark-theme-clr: #202024;
  --lightgray-clr: #b4afaf;
  --accent-violet-clr: #4f2ee8;
  --accent-yellow-clr: #eac645;
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

body {
  color: var(--main-text-clr);
  font-family: DM Sans, sans-serif;
  overflow-x: hidden;
}

